.student-record-list{
	background: lightblue;
	margin: 0px;
}
.student-record{
  padding: 10px;
  gap: 40px;
  font-size: 19px;
  font-size: 19px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Sans-Serif;
  margin: 0px auto;
  border: 1px solid black;
  border-top: none;
}

.name{
  
  color: red;
  font-weight: bold;
  width: 60%;
}
.o-marks{
	width: 20%;
  color: green;
  font-weight: bold;
flex-basis: 0.5;
}
.f-marks{
 
  color: blue;
  font-weight: bold;
flex-basis: 0.8;
}