
*{
	box-sizing: border-box;
}
h2{
	text-align: center;
	padding: 10px;
	color: blue;
	background: aqua;
	border: 1px solid black;
	border-bottom: none;
	margin-top: 10px;
	
}
.main{
	width: 95vw;
	margin: 0px auto;
	box-sizing: border-box;
}
.nof{
	display: flex;
	justify-content: space-between;
	padding: 10px;
	font-size: 18px;
	background-color: pink;
	border: 1px solid black;
}
.n{
  color: red;
  font-weight: bold;
  
}
.o{
  color: green;
  font-weight: bold; 
  
}
.f{
  color: blue;
  font-weight: bold;

}

@media screen and (min-width:600px) {
	.main{
	width: 400px;
	
		
	}
}